<template>
	<div class="conversation_container">
		<h1 class="title">ÏàòÎ¶¨ÏôÄ ÎåÄÌôîÌïòÍ∏∞</h1>
		<BubbleHonsuri />
		<BubbleUser />
		<BubbleResult v-if="this.$store.state.qna_count >= 16" />
	</div>
</template>

<style scoped>
	.conversation_container {
		width: 1280px;
		background-color: #ffffff !important;
		box-shadow: 1px 1px 2px #00000029;
		border-radius: 8px;
		margin-inline: auto;
		padding-inline: 60px;
		padding-block: 40px;
	}
	.title {
		font-size: 36px;
		font-weight: 700;
		text-align: start;
	}
	.title:after {
		content: "";
		display: block;
		width: 1280px;
		border-bottom: 1px solid #767676;
		margin-top: 30px;
	}
</style>

<script>
	import BubbleHonsuri from "@/components/conversation/BubbleHonsuri.vue";
	import BubbleUser from "@/components/conversation/BubbleUser.vue";
	import BubbleResult from "@/components/conversation/BubbleResult.vue";
	import axios from "axios";

	export default {
		name: "SectionConversation",
		components: {
			BubbleHonsuri,
			BubbleUser,
			BubbleResult,
		},
		data() {
			return {};
		},
		// Ï∂îÌõÑ API Ìò∏Ï∂ú Î∂ÄÎ∂Ñ Ï†ïÎ¶¨ !!!!
		// Ï≤òÏùåÏóê ÌòºÏàòÎ¶¨ ÎåÄÌôî ÌïòÍ∏∞ Îç∞Ïù¥ÌÑ∞Î•º Î∂àÎü¨Ïò§Í∏∞ ÏúÑÌï®
		created() {
			const mbti_url = "https://honsuri-backend.herokuapp.com/mbti/";
			const mbti_id = this.$store.state.qna_count;
			// ÏÇ¨Ïö©ÏûêÍ∞Ä mbti test ÎãµÏïàÏùÑ ÏÑ†ÌÉùÌï† Îïå ÎßàÎã§, count++ ÎêòÎäîÎç∞, Í∑∏ÎïåÏùò countÎ•º url Îí§Ïóê Î∂ôÏó¨ÏÑú API ÌÜµÏã†
			const full_url = mbti_url + mbti_id;
			axios
				.get(full_url)
				.then(res => {
					// console.log(res.data);
					// console.log(res.data.question);
					this.$store.state.question = res.data.question;
					// console.log(res.data.answer);
					this.$store.state.answer = res.data.answer;
				})
				.catch(err => {
					console.log(err);
				});
		},
		// qna_count Í∞íÏù¥ ÏóÖÎç∞Ïù¥Ìä∏ Îê† Îïå ÎßàÎã§, ÏÉàÎ°úÏö¥ ÏßàÎ¨∏ÏùÑ Î∂àÎü¨Ïò§Í∏∞ ÏúÑÌï®
		updated() {
			const mbti_url = "https://honsuri-backend.herokuapp.com/mbti/";
			const mbti_id = this.$store.state.qna_count;
			// ÏÇ¨Ïö©ÏûêÍ∞Ä mbti test ÎãµÏïàÏùÑ ÏÑ†ÌÉùÌï† Îïå ÎßàÎã§, count++ ÎêòÎäîÎç∞, Í∑∏ÎïåÏùò countÎ•º url Îí§Ïóê Î∂ôÏó¨ÏÑú API ÌÜµÏã†
			const full_url = mbti_url + mbti_id;
			// qna_count Í∞íÏù¥ 16Î≥¥Îã§ ÏûëÏùÑ Îïå, Îã§Ïùå ÏßàÎ¨∏ÏùÑ Î∂àÎü¨Ïò¥.
			if (this.$store.state.qna_count < 16) {
				axios
					.get(full_url)
					.then(res => {
						this.$store.state.question = res.data.question;
						this.$store.state.answer = res.data.answer;
					})
					.catch(err => {
						console.log(err);
					});
			}
			// qua_countÍ∞Ä 16Ïù¥ ÎêòÎ©¥ ÎåÄÌôî Ï¢ÖÎ£å -> MBTI Í≤∞Í≥ºÍ∞í Î∞òÌôò
			if (this.$store.state.qna_count === 16) {
				// console.log("MBTI ÌÖåÏä§Ìä∏ ÎÅù üéâ");
				let dictMBTI = this.$store.state.dictMBTI;
				// ÏÇ¨Ïö©ÏûêÏùò MBTI Í≤∞Í≥ºÍ∞í Î∞òÌôò
				for (let key in dictMBTI) {
					// 2 Ïù¥ÏÉÅÏùò Í∞íÏùÑ Í∞ÄÏßÄÍ≥† ÏûàÎäî keyÎ°ú MBTI Í≤∞Í≥ºÍ∞í Î∞òÌôò
					if (dictMBTI[key] >= 2) {
						this.$store.state.mbti_result += key;
					}
				}
				console.log(this.$store.state.mbti_result);
			}
		},
	};
</script>



