<template>
	<div class="container">
		<img class="user_img" src="@/assets/images/user.jpeg" />
		<div class="text_container">
			<div class="bubble top">
				<a
					@click="
						COUNT_QnA_ID();
						SELECT_A();
					"
					class="test_text"
					>A. {{ $store.state.answer[Object.keys($store.state.answer)[0]] }} <br />
				</a>
			</div>
			<div class="bubble bottom">
				<a
					@click="
						COUNT_QnA_ID();
						SELECT_B();
					"
					class="test_text"
				>
					B. {{ $store.state.answer[Object.keys($store.state.answer)[1]] }}
				</a>
			</div>
		</div>
	</div>
</template>

<style scoped>
	.container {
		display: flex;
		flex-direction: row-reverse;
		margin-block: 40px;
		display: flex;
	}
	.user_img {
		width: 50px;
		height: 50px;
		margin-left: 20px;
		border-radius: 50%;
	}
	.text_container {
		display: flex;
		flex-direction: column;
	}
	.bubble {
		font-size: 20px;
		font-weight: 600;
		padding-inline: 20px;
		padding-block: 20px;
		background: #ededed;
		box-shadow: 2px 2px 2px #00000029;
		text-align: left;
	}
	.top {
		border-radius: 8px 8px 0px 0px;
		border-bottom: solid 1px #dbdbdb;
	}
	.bottom {
		border-radius: 0px 0px 8px 8px;
	}

	.test_text:hover {
		box-shadow: inset 0 -9px 0 rgb(221, 152, 23);
		cursor: pointer;
	}
</style>

<script>
	import { mapState } from "vuex";

	export default {
		name: "BubbleUser",
		state: {
			...mapState(["answer", "dictMBTI"]),
		},
		methods: {
			COUNT_QnA_ID: function () {
				// userÍ∞Ä ÎãµÎ≥Ä ÏÑ†ÌÉùÏãú count++ (Ïù¥ Í∞íÏùÑ api url Îí∑ Î∂ÄÎ∂ÑÏóê ÎÑ£Ïñ¥Ï§òÏÑú Îã§Ïùå ÏßàÎ¨∏ÏùÑ Î∂àÎü¨Ïò¥)
				this.$store.state.qna_count += 1;
			},
			SELECT_A: function () {
				// dictMBTI Í∞ùÏ≤¥, Ìï¥Îãπ ÎãµÎ≥ÄÏùò KeyÏùò Value ++1
				// Q. Ìï®Ïàò ÎÇ¥ÏóêÏÑú mapStateÎ•º ÏÇ¨Ïö©ÌïòÎäî Î∞©Î≤ïÏù¥ ÏóÜÏùÑÍπå?! ÏΩîÎìú Í∞ÄÎèÖÏÑ±Ïù¥ Îñ®Ïñ¥Ï†∏ÏÑú Ïä¨ÌîÑÍµ∞.
				// Object.keys(this.$store.state.answer)[0] ÏùÄ MBTI Ïú†ÌòïÏù¥ Îã¥Í∏¥ Í∞í (E I S N F J ... Îì±)
				this.$store.state.dictMBTI[Object.keys(this.$store.state.answer)[0]] += 1;
				// console.log(this.$store.state.dictMBTI);
				// console.log("A ÏÑ†ÌÉù.");
			},
			SELECT_B: function () {
				// dictMBTI Í∞ùÏ≤¥, Ìï¥Îãπ ÎãµÎ≥ÄÏùò KeyÏùò Value ++1
				// Object.keys(this.$store.state.answer)[1] ÏùÄ MBTI Ïú†ÌòïÏù¥ Îã¥Í∏¥ Í∞í (E I S N F J ... Îì±)
				this.$store.state.dictMBTI[Object.keys(this.$store.state.answer)[1]] += 1;
				// console.log(this.$store.state.dictMBTI);
				// console.log("B ÏÑ†ÌÉù.");
			},
		},
	};
</script>



