<template>
	<div>
		<div class="container pointer">
			<img class="honsuri_img" src="@/assets/icons/honsuri.png" />
			<a class="honsuri_text" @click="GET_RECOMMEND_COCKTAIL()">
				Í∑∏Î†áÍµ∞Ïöî... Ïó¨Í∏∞Î•º ÎàÑÎ•¥Î©¥ ÏÜêÎãòÏùÑ ÏúÑÌïú ÏπµÌÖåÏùºÏùÑ Ï∂îÏ≤úÌï¥ÎìúÎ¶¥Í≤åÏöî.
			</a>
		</div>
		<div class="container none_img_margin">
			<div class="honsuri_text cocktail_text" v-if="recommend">
				<p>Ïò§ÎäòÏùò Ï∂îÏ≤ú ÏπµÌÖåÏùºÏùÄ...</p>
				<h1>{{ recommend_cock }}</h1>
				<p>"{{ recommend_comment }}"</p>
				<!-- ÏøºÎ¶¨Í∞Ä ÏûàÏúºÎ©¥, `/recipes/${recommend_cock_id}` ÌòïÏãùÏù¥ Îê® -->
				<router-link :to="{ name: 'RecipeDetail', params: { id: this.$store.state.recommend_cock_id } }">
					<button>Î†àÏãúÌîº Î≥¥Îü¨Í∞ÄÍ∏∞</button>
				</router-link>
			</div>
			<div class="honsuri_text" v-if="recommend">
				<img
					class="cock_img"
					v-bind:src="require(`@/assets/recipeImg/${recommend_cock_photo}`)"
					width="230px"
					height="235px"
				/>
			</div>
		</div>
	</div>
</template>

<style scoped>
	.container {
		display: flex;
		margin-block: 30px;
	}
	.honsuri_img {
		width: 50px;
		height: 50px;
		margin-right: 20px;
	}
	.honsuri_text {
		font-size: 20px;
		font-weight: 600;
		padding-inline: 25px;
		padding-block: 25px;
		background: #ededed;
		box-shadow: 2px 2px 2px #00000029;
		border-radius: 8px;
		text-align: left;
	}
	.pointer {
		cursor: pointer;
	}
	.none_img_margin {
		/* Ïù¥ÎØ∏ÏßÄ ÏóÜÎäî ÎåÄÏã†, margin Í∞íÏúºÎ°ú Ïó¨Î∞±ÏùÑ Ï±ÑÏõÄ */
		margin-left: 70px;
	}
	.cocktail_text {
		text-align: center;
	}
	.cocktail_text p {
		font-size: 23px;
		font-weight: 600;
		margin-bottom: 15px;
		margin-top: 10px;
	}
	.cocktail_text h1 {
		font-size: 27px;
		font-weight: 800;
		margin-bottom: 10px;
	}
	.cocktail_text button {
		color: white;
		width: 300px;
		font-size: 20px;
		background-color: #191919;
		margin-top: 30px;
		height: 49px;
		border: none;
		cursor: pointer;
	}
	.cock_img {
		margin-block: auto;
	}
</style>

<script>
	import axios from "axios";
	import { mapState } from "vuex";

	export default {
		name: "BubbleResult",
		data() {
			return {
				recommend: false,
				recommend_photo_url: "",
			};
		},
		computed: {
			...mapState(["recommend_comment", "recommend_cock", "recommend_cock_id", "recommend_cock_photo"]),
		},
		methods: {
			// mbti_resultÎ•º ÌååÎùºÎØ∏ÌÑ∞Î°ú ÌïòÏó¨ Í≤∞Í≥ºÍ∞íÏùÑ Í∞ÄÏ†∏Ïò¨ ÏòàÏ†ï
			GET_RECOMMEND_COCKTAIL: function () {
				const mbti_result = { mbti: this.$store.state.mbti_result };
				axios
					.get("https://honsuri-backend.herokuapp.com/mbti", {
						params: mbti_result,
					})
					.then(res => {
						// console.log("Î∂àÎü¨Ïò§Í∏∞ ÏÑ±Í≥µ");
						this.$store.state.recommend_comment = res.data[0].comment;
						this.$store.state.recommend_cock_id = res.data[0].recipe.id;
						this.$store.state.recommend_cock = res.data[0].recipe.name;
						this.$store.state.recommend_cock_photo = res.data[0].recipe.photo;
						// console.log(this.$store.state.recommend_cock_photo);
						// console.log(res.data[0].comment); // ÏΩîÎ©òÌä∏
						// console.log(res.data[0].recipe.id); // Í≤åÏãúÎ¨º id
						// console.log(res.data[0].recipe.name); // Í≤åÏãúÎ¨º Ïù¥Î¶Ñ
						// console.log(res.data[0].recipe.photo); // Í≤åÏãúÎ¨º ÏÇ¨ÏßÑ ÌååÏùºÎ™Ö
						this.recommend = true; // true Í∞íÏùº Îïå Í≤∞Í≥ºÍ∞íÏùÑ Î≥¥Ïó¨Ï£ºÎäî bubble Ïù¥ show Îê®
					})
					.catch(err => {
						// console.log("Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®");
						console.log(err);
					});
			},
		},
	};
</script>



